*, :before, :after {
  box-sizing: border-box;
  margin-top: 0;
  margin-bottom: 0;
  margin-left: 0;
  margin-right: 0;
  padding: 0;
}

.form-container {
  opacity: .5;
  height: 80px;
  background-color: #789;
  flex-direction: column;
  align-items: center;
  gap: 10px;
  padding: 10px;
  display: flex;
}

.search-form {
  width: 500px;
  height: 40px;
  background: rgba(0, 0, 0, .2);
  border: 1px solid #fff;
  border-radius: 3px;
  margin: auto;
}

input {
  width: 370px;
  float: left;
  color: #ccc;
  background: none;
  border: 0;
  border-radius: 3px 0 0 3px;
  padding: 10px 5px;
}

input:focus {
  background: none;
  outline: 0;
}

.submit-button {
  float: right;
  cursor: pointer;
  height: 40px;
  width: 120px;
  color: #fff;
  background: none;
  border: 0;
  border-left: 1px solid #fff;
  border-radius: 0 3px 3px 0;
  padding: 0;
  position: relative;
}

.submit-button:hover {
  color: #444;
  background: #fff;
}

.submit-button:active {
  box-shadow: 0 0 12px #e1e1e1;
}

.submit-button:focus {
  outline: 0;
}

.gallery {
  margin: 15px;
  flex-flow: wrap;
  flex: 0 1170px;
  gap: 35px;
  margin-left: 25px;
  display: flex;
}

.photo-card {
  max-width: 340px;
  background-size: cover;
  border: 1px solid #eee;
  gap: 30px;
  padding: 5px;
  transition: box-shadow .25s cubic-bezier(.4, 0, .2, 1);
  overflow: hidden;
}

.photo-card:hover {
  box-shadow: 0 1px 1px rgba(0, 0, 0, .12), 0 4px 4px rgba(0, 0, 0, .06), 1px 4px 6px rgba(0, 0, 0, .16);
}

.info {
  flex-direction: row;
  gap: 10px;
  margin-top: 10px;
  display: flex;
}

img {
  width: 100%;
  height: 30vh;
  object-fit: cover;
}

.load-more {
  cursor: pointer;
  color: #ccc;
  opacity: .8;
  background-color: #789;
  border: none;
  border-radius: 5px;
  margin: 20px auto;
  padding: 10px 20px;
  font-weight: bold;
  transition: box-shadow .25s cubic-bezier(.4, 0, .2, 1);
  display: none;
}

.load-more:hover {
  box-shadow: 0 1px 1px rgba(0, 0, 0, .12), 0 4px 4px rgba(0, 0, 0, .06), 1px 4px 6px rgba(0, 0, 0, .16);
}

.sl-image {
  max-width: 300px;
}

